import { generateTrackerForAppAccount } from '../rest/SaaspassExtensionResource';

const loginWithApplication = async (appId, accId) => {
  const tracker = await generateTrackerForAppAccount(accId, appId);
  const url = `${api.baseURL}/rest/loginSAML/extension/sso/${tracker}`;
  await browser.tabs.create({ url });
};

export default loginWithApplication;
